import "./App.css";
import { useState } from "react";
import SubscriptionScreen from "./components/SubscriptionScreen";
import TimesPrimeUI from "./components/TimesPrimeUI";
import QRScreen from "./components/QRScreen";
import { Switch, Route, useHistory } from "react-router-dom";
import CredentialsScreen from "./components/Credentials";
import Verifier from "./components/Verifier";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import HomeIcon from "@mui/icons-material/Home";
import IconButton from "@mui/material/IconButton";

function App() {
  const [loading, setLoading] = useState(false);
  const [did, setDid] = useState("");
  const [credentials, setCredentials] = useState([]);
  const history = useHistory()

  const onSubscribe = () => {
    setLoading(true);
  };
  return (
    <div classname="App">
      <>
        <Box sx={{ flexGrow: 1,zIndex:100}}>
          <AppBar position="static" style={{ background: '#6B11DE'}} >
            <Toolbar >
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2,mt:'12px' }}
                onClick={() => {history.push('/')}}
              >
                <HomeIcon />
              </IconButton>
              <div style={{display:'inline-block',cursor: 'pointer',flex:1}} onClick={() => {history.push('/')}}>
              <img  style={{width:'200px',marginTop:'-2px'}}src="https://static.timesprime.com/3x/timesprime-2.png" alt="Times Prime Logo"></img>
              </div>
              <div style={{cursor:'pointer',width:'150px',display:'inline-block',marginRight:'20px'}} onClick={()=>{history.push('/subscriptions/toi')}}>
              <img style={{width:'100%',}} src="https://asiindia.org/wp-content/uploads/toi-logo.png" />
              </div>
              <div style={{cursor:'pointer',width:'180px',display:'inline-block'}} onClick={()=>{history.push('/subscriptions/et')}}>
              <img  style={{width:'100%',height:'auto'}} src="data:image/png;base64," height="51" width="464" alt="The Economic Times"></img>
              </div>
            </Toolbar>
          </AppBar>
        </Box>
        <Switch>
          <Route path="/" exact>
            <TimesPrimeUI onSubscribe={onSubscribe} />
          </Route>
          <Route path="/subscription" exact>
            <SubscriptionScreen
              did={did}
              setCredentials={setCredentials}
              setDid={setDid}
              loading={loading}
              setLoading={setLoading}
              issueCredentials
            />
          </Route>
          <Route path="/scandid" exact>
            <QRScreen setDid={setDid} />
          </Route>
          <Route path="/creds" exact>
            <CredentialsScreen credentials={credentials} />
          </Route>
          <Route
            path="/subscriptions/:service"
            exact
            render={(props) => <Verifier {...props} />}
          />
        </Switch>
      </>
    </div>
  );
}

export default App;
